<template>
  <van-cell class="comment-item" :title="comment.content">
    <template #icon>
      <van-image class="avatar" fit="cover" round :src="comment.aut_photo" />
    </template>

    <template #title>
      <div class="user-name">{{ comment.aut_name }}</div>
    </template>

    <template #label>
      <p class="comment-content">{{ comment.content }}</p>
      <div class="bottom-info">
        <div class="publish-date">{{ comment.pubdate | relativeTime }}</div>
        <van-button class="reply-btn" round
        @click="$emit('reply-click',comment)"
          >回复 {{ comment.reply_count }}
        </van-button>
      </div>
    </template>
    <template #right-icon>
      <van-button
        class="like-btn"
        :icon="comment.is_liking ? 'good-job' : 'good-job-o'"
        :class="{ like: comment.is_liking }"
        :loading="btnLoading"
        @click="onCommentLike"
      >
        {{ comment.like_count || "赞" }}</van-button
      >
    </template>
  </van-cell>
</template>

<script>
import { deleteCommentLikeAPI, addCommentLikeAPI } from '../../../api/index.js'
export default {
  name: 'CommentItem',
  props: {
    comment: {
      type: Object,
      default: () => ({})
    }
  },
  data () {
    return {
      btnLoading: false,
      commentClone: this.comment
    }
  },
  methods: {
    async onCommentLike () {
      this.btnLoading = true
      try {
        if (this.comment.is_liking) {
          await deleteCommentLikeAPI(this.comment.com_id)
          this.commentClone.like_count--
        } else {
          await addCommentLikeAPI(this.comment.com_id)
          this.commentClone.like_count++
        }
        this.commentClone.is_liking = !this.comment.is_liking
      } catch (error) {
        this.$toast('操作失败，请重试')
      }
      setTimeout(() => {
        this.btnLoading = false
      }, 500)
    }
  }
}
</script>

<style lang="less" scoped>
.comment-item {
  .avatar {
    width: 70px;
    height: 70px;
    margin-right: 26px;
  }
  .user-name {
    color: #406599;
    font-size: 26px;
  }
  .comment-content {
    font-size: 32px;
    color: #222222;
    word-break: break-all;
    text-align: justify;
  }
  .bottom-info {
    display: flex;
    align-items: center;

    .publish-date {
      font-size: 20px;
      color: #222222;
      margin-right: 26px;
    }
    .reply-btn {
      width: 140px;
      height: 48px;
      font-size: 24px;
      color: #222222;
    }
  }
  .like-btn {
    border: none;
    height: 30px;
    padding: 0;
    font-size: 20px;
    margin-right: 10px;
  }
  .like {
    color: red;
  }
}
</style>
