<template>
 <div class="collected">
  <van-icon
    :name="value ? 'star' : 'star-o'"
    :class="{collected:value}"
    :loading ="btnLoading"
    @click="onCollect"
  />
 </div>
</template>

<script>
import { deleteCollectAPI, addCollectAPI } from '../api/index.js'

export default {
  name: 'CollectArticle',

  data () {
    return {
      btnLoading: false
    }
  },
  props: {
    value: {
      type: Boolean,
      default: true
    },
    articleId: {
      type: String,
      default: ''
    }
  },
  methods: {
    async onCollect () {
      this.btnLoading = true
      try {
        if (this.value) {
          await deleteCollectAPI(this.articleId)
        } else {
          await addCollectAPI(this.articleId)
        }
        this.$emit('input', !this.value)
      } catch (error) {
        if (error.response && error.response.status === 400) {
          this.$toast('你不能关注自己')
        } else {
          this.$toast('操作失败，请重试')
        }
      }
      setTimeout(() => {
        this.btnLoading = false
      }, 500)
    }
  }
}
</script>

<style lang="less" scoped>
.collected {
  .van-icon {
    color: #ffa500;
  }
}
</style>
