<template>
  <div class="comment-reply">
    <!-- 头部 -->
    <van-nav-bar
      :title="
        comment.reply_count > 0 ? `${comment.reply_count}条回复` : '暂无回复'
      "
      left-arrow
      fixed
      ><template #left>
        <van-icon name="cross" @click="$emit('close')" />
      </template>
    </van-nav-bar>

    <div class="scroll-wrap">
      <comment-item :comment="comment"></comment-item>
      <van-cell>全部回复</van-cell>
      <comment-list ref="comment-list" :targetId="comment.com_id" type='c'></comment-list>
    </div>

    <!-- 底部 -->
    <div class="reply-bottom">
      <van-button
        class="reply-btn"
        round
        size="small"
        @click="isPostShow = true"
      >写评论
      </van-button>
    </div>

    <van-popup
      v-model="isPostShow"
      position="bottom"
    >
    <comment-post
      :targetId="comment.com_id"
      @post-success="onPostSuccess"
      :articleId="$route.params.articleId"
    ></comment-post>
    </van-popup>
  </div>
</template>

<script>
import commentItem from './comment-item.vue'
import CommentList from './comment-list.vue'
import CommentPost from './comment-post.vue'
export default {
  components: {
    commentItem,
    CommentList,
    CommentPost
  },
  name: 'CommentReply',
  props: {
    comment: {
      type: Object,
      default: () => ({})
    }
  },
  data () {
    return {
      isPostShow: false,
      commentClone: this.comment
    }
  },
  methods: {
    onPostSuccess (item) {
      this.commentClone.reply_count++
      this.isPostShow = false

      this.$refs['comment-list'].unshiftComment(item)
    }
  }
}
</script>

<style lang="less" scoped>
.comment-reply {
  .van-nav-bar {
    background-color: #fff;

    :deep(.van-nav-bar__title) {
      color: #000;
    }
    .van-icon {
      color: #3296fa;
    }
  }
  .scroll-wrap {
    padding-top: 92px;
    padding-bottom: 88px;
    overflow-y: auto;
    background-color: #fff;
  }
  .reply-bottom {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 88px;
    border-top: 1px solid #d8d8d8;
    background-color: #fff;

    .reply-btn {
      width: 280px;
      height: 46px;
      line-height: 46;
      border: 2px solid #eee;
      color: #a7a7a7;
    }
  }
}
</style>
