<template>
  <van-button
    class="follow-btn"
    type="info"
    round
    size="small"
    icon="plus"
    @click="onFollow"
    v-if="isFollowed"
    :loading="btnLoading"
    >关注</van-button
  >
  <van-button
    class="follow-btn"
    round
    size="small"
    @click="onFollow"
    v-else
    :loading="btnLoading"
    >已关注</van-button
  >
</template>

<script>
import { deleteFollowAPI, addFollowAPI } from '../api/index.js'

export default {
  name: 'FollowUser',
  model: {
    prop: 'isFollowed', // 默认值为value
    event: 'update-follow' // 默认值为input
  },
  data () {
    return {
      btnLoading: false
    }
  },
  props: {
    isFollowed: {
      type: Boolean,
      default: true
    },
    userId: {
      type: String,
      default: ''
    }
  },
  methods: {
    async onFollow () {
      this.btnLoading = true
      try {
        if (this.isFollowed) {
          await deleteFollowAPI(this.userId)
        } else {
          await addFollowAPI(this.userId)
        }
        this.$emit('update:isFollowed', !this.isFollowed)
      } catch (error) {
        if (error.response && error.response.status === 400) {
          this.$toast('你不能关注自己')
        } else {
          this.$toast('操作失败，请重试')
        }
      }
      setTimeout(() => {
        this.btnLoading = false
      }, 500)
    }
  }
}
</script>

<style lang="less" scoped>
</style>
