<template>
  <div class="collected">
    <van-icon
      :name="attitude === 1 ? 'good-job' : 'good-job-o'"
      :class="{ collected: attitude === 1 }"
      :loading="btnLoading"
      @click="onCollect"
    />
  </div>
</template>

<script>
import { deleteLikeAPI, addLikeAPI } from '../api/index.js'

export default {
  name: 'LikeArticle',
  data () {
    return {
      btnLoading: false
    }
  },
  props: {
    attitude: {
      type: Number,
      default: -1
    },
    articleId: {
      type: String,
      default: ''
    }
  },
  inject: {
    articleIdPro: {
      type: String,
      default: ''
    }
  },
  mounted () {
    console.log(this.articleIdPro, 'iniect')
  },
  methods: {
    async onCollect () {
      this.btnLoading = true
      let status = this.attitude
      try {
        if (this.attitude === 1) {
          status = -1
          await deleteLikeAPI(this.articleId)
        } else {
          status = 1
          await addLikeAPI(this.articleId)
        }
        this.$emit('update:attitude', status)
      } catch (error) {
        if (error.response && error.response.status === 400) {
          this.$toast('你不能关注自己')
        } else {
          this.$toast('操作失败，请重试')
        }
      }
      setTimeout(() => {
        this.btnLoading = false
      }, 500)
    }
  }
}
</script>

<style lang="less" scoped>
.collected {
  .van-icon {
    color: red;
  }
}
</style>
