<template>
  <div class="comment-list">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      :error.sync="error"
      error-text="请求失败，点击重新加载"
    >
      <comment-item
        v-for="(commentItem, index) in list"
        :key="index"
        :comment="commentItem"
        @reply-click="$emit('reply-click',$event)"
      ></comment-item>
    </van-list>
  </div>
</template>

<script>
import CommentItem from './comment-item.vue'
import { getCommentsAPI } from '../../../api/index.js'
export default {
  name: 'CommentList',
  components: {
    CommentItem
  },
  props: {
    targetId: {
      type: String,
      default: ''
    },
    totalCount: { // 总数据条数
      type: Number,
      default: 0
    },
    type: {
      type: String,
      default: 'a'
    }
  },
  data () {
    return {
      list: [],
      error: false,
      loading: false,
      finished: false,
      offset: null // 请求下一页数据的页码
    }
  },
  methods: {
    async onLoad () {
      try {
        const { data: res } = await getCommentsAPI({
          type: this.type,
          source: this.targetId,
          offset: this.offset,
          limit: 10
        })
        this.list.push(...res.data.results)
        this.$emit('update:totalCount', res.data.total_count)
        this.loading = false
        if (res.data.results.length) {
          this.offset = res.data.last_id
        } else {
          this.finished = true
        }
      } catch (error) {
        this.error = true
        this.loading = false
      }
    },
    unshiftComment (item) {
      this.list.unshift(item.new_obj)
    }
  }
}
</script>

