<template>
  <div class="comment-post">
    <van-field
      class="post-field"
      v-model.trim="message"
      rows="2"
      autosize
      type="textarea"
      maxlength="50"
      placeholder="优质评论优先显示"
      show-word-limit
    />
    <van-button
      class="post-btn"
      size="small"
      @click="onAddComment"
      :disabled="!message.length"
      >发布
    </van-button>
  </div>
</template>

<script>
import { addCommentAPI } from '../../../api/index.js'
export default {
  name: 'CommentPost',
  props: {
    targetId: {
      type: String,
      default: ''
    },
    articleId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      message: ''
    }
  },
  methods: {
    async onAddComment () {
      try {
        const messageTemp = this.message.trim()
        if (!messageTemp.length) {
          return
        }
        const { data: res } = await addCommentAPI({
          target: this.targetId,
          content: this.message,
          art_id: this.articleId || null
        })
        console.log(res)
        this.message = ''
        this.$emit('post-success', res.data)
        this.$toast.success('发布成功')
      } catch (error) {
        this.$toast.fail('发布失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.comment-post {
  display: flex;
  align-items: center;
  padding: 32px 0 32px 32px;

  .post-field {
    background-color: #f5f6f7;
  }

  .post-btn {
    width: 150px;
    border: none;
    padding: 0;
    color: #6ba3d8;
  }
}
</style>
