<template>
  <div class="article-container">
    <!-- 导航栏 -->
    <van-nav-bar
      left-arrow
      title="黑马头条"
      @click-left="$router.back()"
      fixed
    />

    <div class="main-wrap">
      <!-- 加载中 -->
      <div class="loading-wrap" v-if="loading">
        <van-loading color="#1989fa" vertical>加载中</van-loading>
      </div>

      <!-- 文章详情 -->
      <div class="article-detail" v-else-if="article.title">
        <!-- 文章标题 -->
        <h1 class="article-title">{{ article.title }}</h1>

        <!-- 用户信息 -->
        <van-cell class="user-info">
          <template #icon>
            <van-image
              class="avatar"
              fit="cover"
              round
              :src="article.aut_photo"
            />
          </template>
          <template #title>
            <div class="user-name">{{ article.aut_name }}</div>
          </template>
          <template #label>
            <div class="publish-date">{{ article.pubdate | relativeTime }}</div>
          </template>
          <template #right-icon>
            <follow-user
              :userId="article.aut_id"
              :isFollowed.sync="article.is_followed"
            ></follow-user>
          </template>
        </van-cell>

        <!-- 文章内容 -->
        <div
          class="article-content markdown-body"
          v-html="article.content"
          ref="article-content"
        ></div>
        <van-divider>正文结束</van-divider>
        <!-- 评论 -->
        <comment-list
          ref="comment-list"
          :targetId="articleId"
          :totalCount.sync="totalCommentCount"
          @reply-click="onReplyClick"
        ></comment-list>
      </div>

      <!-- 加载失败：404 -->
      <div class="error-wrap" v-else-if="errStatus === 404">
        <van-icon name="failure" />
        <p class="text">该资源不存在或已删除！</p>
      </div>

      <!-- 加载失败：其它未知错误（例如网络原因或服务端异常） -->
      <div class="error-wrap" v-else>
        <van-icon name="failure" />
        <p class="text">内容加载失败！</p>
        <van-button class="retry-btn" @click="loadArticleInfo"
          >点击重试</van-button
        >
      </div>
    </div>

    <!-- 底部区域 -->
    <div class="article-bottom">
      <van-button
        class="comment-btn"
        type="default"
        round
        size="small"
        @click="isPostShow = true"
        >写评论</van-button
      >
      <van-icon name="comment-o" :badge="totalCommentCount" />
      <collect-article
        :articleId="article.art_id"
        v-model="article.is_collected"
      ></collect-article>
      <like-article
        :articleId="article.art_id"
        :attitude.sync="article.attitude"
      ></like-article>
    </div>

    <van-popup v-model="isPostShow" position="bottom">
      <comment-post
        :targetId="article.aut_id"
        @post-success="onPostSuccess"
      ></comment-post>
    </van-popup>
    <van-popup
      v-model="isReplyShow"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <comment-reply
        :comment="currentComment"
        @close="isReplyShow = false"
        v-if="isReplyShow"
      ></comment-reply>
    </van-popup>
  </div>
</template>

<script>
import CommentPost from './components/comment-post.vue'
import CommentList from './components/comment-list.vue'
import CollectArticle from '../../components/collect-article.vue'
import LikeArticle from '../../components/like-article.vue'
import FollowUser from '../../components/follow-user.vue'
import { ImagePreview } from 'vant'
import { getArticleByIdAPI } from '../../api/index.js'
import CommentReply from './components/comment-reply.vue'
export default {
  name: 'ArticleIndex',
  data () {
    return {
      article: {},
      loading: true,
      errStatus: 0,
      isPostShow: false,
      totalCommentCount: 0,
      isReplyShow: false,
      currentComment: {}
    }
  },
  components: {
    FollowUser,
    CollectArticle,
    LikeArticle,
    CommentList,
    CommentPost,
    CommentReply
  },
  props: {
    articleId: {
      type: String,
      default: ''
    }
  },
  provide: function () {
    return {
      articleIdPro: this.articleId
    }
  },
  created () {
    this.loadArticleInfo()
  },
  methods: {
    async loadArticleInfo () {
      this.loading = true
      try {
        const { data: res } = await getArticleByIdAPI(this.articleId)
        console.log(res)
        this.article = res.data
        setTimeout(() => {
          this.previewImage()
        }, 0)
      } catch (error) {
        if (error.response && error.response.status === 404) {
          this.errStatus = 404
        }
        this.$toast('获取文章失败')
      }
      this.loading = false
    },
    previewImage () {
      const articleContent = this.$refs['article-content']
      if (!articleContent) return
      const images = articleContent.querySelectorAll('img')
      const imageList = [...images].map(img => img.src)
      images.forEach((img, index) => {
        img.onclick = () => {
          ImagePreview({
            images: imageList,
            startPosition: index
          })
        }
      })
    },
    onPostSuccess (item) {
      this.isPostShow = false

      this.$refs['comment-list'].unshiftComment(item)
      console.log(item)
    },
    onReplyClick (comment) {
      this.currentComment = comment || {}
      console.log(comment)
      this.isReplyShow = true
    }
  }
}
</script>

<style lang="less" scoped>
@import "./css/github-markdown.css";
.article-container {
  .main-warp {
    padding-top: 92px;
    padding-bottom: 88px;
    overflow-y: auto;
    background-color: #fff;
  }
  .loading-wrap {
    padding: 200px 32px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .article-detail {
    .article-title {
      font-size: 40px;
      padding: 90px 32px 40px;
      margin: 0;
      color: #3a3a3a;
    }
    .user-info {
      padding: 0 32px;
      .avatar {
        width: 70px;
        height: 70px;
        margin-right: 12px;
      }

      .user-name {
        font-size: 24px;
        color: #3a3a3a;
      }
      .van-cell__label {
        margin: 0;
        .publish-date {
          font-size: 24px;
          color: #b7b7b7;
        }
      }
      .follow-btn {
        width: 180px;
        height: 58px;
      }
    }
    .article-content {
      padding: 50px 32px;
    }
  }
  .error-wrap {
    padding: 200px 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .van-icon {
      font-size: 130px;
      color: #b4b4b4;
    }
    .text {
      font-size: 34px;
      color: #666;
      margin: 32px 0 64px;
    }
    .retry-btn {
      width: 280px;
      height: 70px;
      line-height: 70px;
      border: 1px solid #c3c3c3;
      font-size: 30px;
      color: #666666;
    }
  }
  .article-bottom {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 88px;
    border-top: 1px solid #d8d8d8;
    background-color: #fff;
    .comment-btn {
      width: 280px;
      height: 46px;
      line-height: 46;
      border: 2px solid #eee;
      color: #a7a7a7;
    }

    .van-icon {
      font-size: 40px;
      .van-info {
        font-size: 16px;
        background-color: #e22829;
      }
    }
  }
}
</style>
